import Index from '@/view/index.vue'
export default [
  {
    path: '/',
    redirect: `/home/homepage`
  },
  {
    // 当某个路由有子级路由时，父级路由需要一个默认的路由，所以父级路由不能定义name属性
    path: '/home',
    component: Index,
    children: [
      {
        path: '/',
        // name: 'home',
        redirect: `/home/homepage`,
        component: () => import('@/view/components/data-grid')
      },
      // 以“/”开头的嵌套路径会被当作根路径，所以子路由上不用加“/”;在生成路由时，
      {
        path: 'homepage',
        name: 'home',
        component: () => import('@/view/components/homepage')
      },
      {
        path: 'dataGrid',
        name: 'dataGrid',
        component: () => import('@/view/components/data-grid')
      }
    ]
  }
]